.logo {
  display: block;
  max-width: 272px;
  margin: 0 auto;

  @media (max-width: $mobile) {
    position: relative;
    z-index: 1;
  }
  @media (max-width: 425px) {
    max-width: 180px;
  }

  .menu-open .page-header & {
    & svg path {
      fill: $c-dark;
    }
  }

  & svg {
    width: 183px;
    height: 19px;

    fill: $c-gold;

    -ms-interpolation-mode: bicubic;

    @media (min-width: $mobile) {
      width: 100%;
      height: auto;
    }

    & path {
      fill: $c-gold;
    }
  }
}

.scroll-up .page-header .logo svg path {
  fill: $c-dark;
}