.partners {
  padding-top: 87px;
  padding-bottom: 108px;
  background-color: #ffffff;

  @media (max-width: $mobile) {
    padding-top: 46px;
    padding-bottom: 76px;
  }

  &__wrapper {
    @include container;
    max-width: 1500px;
    margin: 0 auto;
  }

  & h2 {
    padding-bottom: 15px;
    @include titleNoColor;
    @include lineBeforeButtom;
  }

  &__slider {
    padding-top: 75px;
    overflow: hidden;

    & .swiper-container {
      @media (max-width: $mobile-xs) {
        padding: 0 60px;
      }
    }

    & .swiper-wrapper {
      padding: 0 90px;

      @media (max-width: $tablet) {
        padding: 0;
      }
    }

    & .swiper-slide {
      & img {
        object-fit: contain;
        vertical-align: middle;
      }
    }

    & .swiper-button-next {
      outline: none;

      &:focus,
      &:active {
        outline: none;
      }

      @media (max-width: $tablet) {
        right: 0;
        background-color: #fff;
        font-size: 23px;
      }

      @media (max-width: 400px) {
        right: 0;
      }

      &::after {
        color: $c-gold;

        @media (max-width: $tablet) {
          color: rgba(66, 75, 90, 0.6);
        }
      }
    }

    & .swiper-button-prev {
      outline: none;

      &:focus,
      &:active {
        outline: none;
      }

      @media (max-width: $tablet) {
        left: 5px;
        background-color: #fff;
        font-size: 23px;
      }

      @media (max-width: 400px) {
        left: 0;
      }

      &::after {
        color: $c-gold;

        @media (max-width: $tablet) {
          color: rgba(66, 75, 90, 0.6);
        }
      }
    }
  }
}
