.page-header {
  z-index: 9;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;

  padding-top: 83px;
  padding-bottom: 83px;
  padding-left: 65px;
  padding-right: 96px;

  transition: 0.5s;

  @media (max-width: 1580px) {
    padding-left: 40px;
    padding-right: 40px;
  }

  @media (max-width: 1280px) {
    padding-left: 0;
    padding-right: 0;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  @media (max-width: $mobile) {
    padding-top: 38px;
    padding-bottom: 24px;
    max-height: 94px;

    &::before {
      content: "";
      z-index: 9;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 90%;
      height: 1px;
      background-color: rgba(255, 255, 255, 0.15);
    }

    .menu-open & {
      &::before {
        background-color: rgba(20, 18, 21, 0.2);
      }
    }
  }

  &__wrapper {
    max-width: 1920px;
    margin: 0 auto;

    @include container;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  &__wrapper-mob {
    @media (max-width: 1180px) {
      order: 3;
    }
  }

  &__logo {
    width: 15%;

    @media (max-width: 1180px) {
      order: 2;

      width: 272px;
      display: flex;
      align-self: start;
      text-align: center;

      .menu-open & {
        width: 89%;
        margin-right: 39px;
      }
    }

    @media (max-width: $mobile) {
      .menu-open & {
        width: 80%;
      }
    }

    @media (max-width: $mobile-xs) {
      width: 70%;
      margin-right: 39px;
      & .logo {
        max-width: 100%;
      }
      .menu-open & {
        width: 70%;
      }
    }
  }

  &__nav {
    width: auto;
    @media (max-width: 1180px) {
      display: none;
      padding-right: 0;
    }

    @media (min-width: 1920px) {
      padding-right: 55px;
    }

    .menu-open & {
      position: relative;
      top: 94px;
      display: block;
      overflow: scroll;
      height: calc(100% - 94px);
      padding-bottom: 45px;
    }
  }

  &__phone {
    width: 15%;
    display: flex;

    @media (max-width: 1180px) {
      order: 1;
    }

    @media (max-width: 1440px) {
      width: auto;
      &.phone {
        font-size: 0;
      }
    }

    @media (max-width: 420px) {
      display: flex;
      margin-right: 0;
    }

    @media (min-width: 1500px) {
      & svg {
        display: none;
      }
    }
    & svg {
      display: none;
      fill: #b1afa9;

      @media (max-width: 1440px) {
        display: flex;
      }

      .scroll-up & {
        fill: $c-dark;
      }

      .menu-open & {
        fill: $c-dark;
      }
    }
  }

  &__btn-burger {
    display: none;
    outline: none;
    @include btn-reset;

    @media (max-width: 1180px) {
      display: block;
      position: absolute;
      top: 60px;
      right: 30px;
    }

    @media (max-width: $mobile) {
      top: 42px;
      right: 30px;
    }

    @media (max-width: $mobile-xs) {
      right: 30px;
    }

    &.active {
      & svg {
        width: 29px;
        height: 29px;

        & rect {
          fill: $c-dark;
          width: 100%;

          &:nth-child(2) {
            opacity: 0;
          }

          &:first-child {
            transform: rotate(45deg) translate(-3px, -8px);
          }
          &:last-child {
            transform: rotate(-45deg) translate(-8px, 0);
          }
        }
      }
    }

    & svg {
      & rect {
        fill: #b1afa9;

        transition: 0.3s;

        .scroll-up & {
          fill: $c-dark;
        }
      }
    }
  }

  &__footer-mob {
    display: none;

    font-style: normal;
    font-weight: normal;
    font-size: 10px;
    line-height: 28px;

    text-align: center;
    letter-spacing: 0.3px;
    text-transform: uppercase;

    color: #141215;

    .menu-open & {
      display: flex;
      margin-top: auto;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    & .social {
      padding-top: 18px;
    }
  }
}

.menu-open {
  overflow: hidden;
}

.menu-open .page-header__wrapper-mob {
  display: block;
  // overflow-y: scroll;
  height: 100vh;
  background-color: #e5e5e5;
}

.menu-open .page-header__btn-burger {
  z-index: 10;
}
.menu-open .page-header__logo,
.menu-open .page-header__phone {
  z-index: 9;
}
.menu-open .page-header__wrapper-mob {
  z-index: 8;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}

.scroll-down .page-header,
.scroll-down .page-header__btn-burger {
  transform: translate3d(0, -100%, 0);
}

.scroll-up .page-header {
  padding: 2rem 0;
  transform: none;
}

.scroll-up .page-header__btn-burger {
  top: 2rem;
  right: 2rem;
}

.scroll-up .page-header .phone {
  color: $c-dark;
}

.scroll-up:not(.menu-open) .page-header {
  background-color: #ffffff;
}
