.projects {
  padding-top: 80px;
  padding-bottom: 70px;
  background-color: #f7f5f1;

  @media (max-width: $mobile) {
    padding-top: 28px;
    padding-bottom: 15px;
  }

  &__wrapper {
    @include container;
  }

  & h2 {
    padding-bottom: 10px;
    @include titleNoColor;

    &::before {
      position: absolute;
      content: "";
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 127px;
      height: 3px;
      background-color: #ccbb9e;
    }
  }

  &__slider {
    padding-top: 62px;

    @media (max-width: $mobile) {
      padding-left: 20px;
      padding-right: 20px;
      padding-top: 42px;
    }

    & .swiper-slide {
      max-width: 1312px;
      margin: 0 auto;

      @media (max-width: 1440px) {
        max-width: 1000px;
      }

      @media (max-width: 1280px) {
        max-width: 800px;
      }

      @media (max-width: 960px) {
        max-width: 760px;
      }

      & img {
        width: 100%;
      }
    }

    & .swiper-slide-prev,
    & .swiper-slide-next {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(
          0deg,
          rgba(255, 255, 255, 0.5),
          rgba(255, 255, 255, 0.5)
        );
      }
    }

    & .swiper-button-next {
      outline: none;

      &:focus,
      &:active {
        outline: none;
      }

      @media (max-width: $mobile) {
        right: 0;
        top: 0;
        width: 34px;
        height: 34px;
        background-color: rgba(255, 255, 255, 0.55);
      }
    }

    & .swiper-button-prev {
      outline: none;

      &:focus,
      &:active {
        outline: none;
      }

      @media (max-width: $mobile) {
        left: 0;
        top: 0;
        width: 34px;
        height: 34px;
        background-color: rgba(255, 255, 255, 0.55);
      }
    }

    & .swiper-button-next:after,
    & .swiper-button-prev:after {
      color: rgba(66, 75, 90, 0.6);

      @media (max-width: $mobile) {
        color: #ffffff;
        font-size: 20px;
      }
    }
  }

  &__slider-btn {
    z-index: 9;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 50px;
    transform: translate(-50%, -50%);
    max-width: 1500px;
    margin: 0 auto;

    @media (max-width: 1440px) {
      max-width: 1150px;
    }

    @media (max-width: 1280px) {
      max-width: 950px;
    }

    @media (max-width: 960px) {
      max-width: 950px;
    }
  }

  &__caption {
    padding-top: 36px;
    padding-bottom: 36px;

    @media (max-width: $mobile) {
      display: flex;
      flex-wrap: wrap;
      flex-direction: row-reverse;
      justify-content: space-between;
      align-items: center;
      padding-top: 0;

      & p {
        padding-top: 15px;
      }
    }

    & p {
      font-family: $font-alt;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 128%;
      letter-spacing: 2px;
      text-transform: uppercase;

      @include fluid-type(420px, 1920px, 10px, 12px);
    }

    & span {
      padding-left: 5px;
      color: #bf9c62;
    }

    & h3 {
      padding-top: 15px;
      font-family: $font-title;
      font-style: normal;
      font-weight: normal;
      font-size: 42px;
      line-height: 51px;
      letter-spacing: 2px;

      @include fluid-type(420px, 1920px, 24px, 42px);

      @media (max-width: $mobile) {
        line-height: 29px;
        padding-top: 10px;
      }
    }
  }
}
