.hero {
  position: relative;
  padding-top: 311px;
  padding-bottom: 311px;
  margin-bottom: 32px;
  background-color: $c-dark;

  @media (max-width: $mobile) {
    padding-top: 260px;
  }

  &::before {
    z-index: 1;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, #1a1919 0%, rgba(26, 25, 25, 0) 34.54%),
      linear-gradient(0deg, rgba(26, 25, 25, 0.6), rgba(26, 25, 25, 0.6));
  }

  &__slider {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    & .swiper-container {
      width: 100%;
      height: 100%;
    }

    & .swiper-slide {
      background-size: cover;
      background-position: center;
    }

    & .swiper-pagination {
      width: 100%;
      left: 50%;
      bottom: -35px;
      transform: translateX(-50%);
      z-index: 99999;
      cursor: pointer;

      @media (max-width: $mobile) {
        bottom: -48px;
      }
    }

    & .swiper-pagination-bullet {
      margin: 0 10px;
      width: 59px;
      height: 3px;
      border-radius: 0;
      opacity: 1;
      background-color: rgba(204, 187, 158, 0.31);
    }

    & .swiper-pagination-bullet-active {
      background-color: #ccbb9e;
      border: none;
      outline: none;

      &:focus,
      &:active {
        outline: none;
      }
    }
  }

  &__slide {
    background-repeat: no-repeat;
    background-size: cover;
    background-color: $c-dark;

    &--1 {
      background-image: url(../img/hero/1-mob.jpg);

      @media (min-width: $tablet) {
        background-image: url(../img/hero/1.jpg);
      }

      @media (min-resolution: 120dpi), (min-resolution: 1.25dppx) {
        background-image: url(../img/hero/1-mob@2x.jpg);
      }

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url(../img/hero/1@2x.jpg);
      }
    }

    &--2 {
      background-image: url(../img/hero/2-mob.jpg);

      @media (min-width: $tablet) {
        background-image: url(../img/hero/2.jpg);
      }

      @media (min-resolution: 120dpi), (min-resolution: 1.25dppx) {
        background-image: url(../img/hero/2-mob@2x.jpg);
      }

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url(../img/hero/2@2x.jpg);
      }
    }

    &--3 {
      background-image: url(../img/hero/3-mob.jpg);

      @media (min-width: $tablet) {
        background-image: url(../img/hero/3.jpg);
      }

      @media (min-resolution: 120dpi), (min-resolution: 1.25dppx) {
        background-image: url(../img/hero/3-mob@2x.jpg);
      }

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url(../img/hero/3@2x.jpg);
      }
    }
  }

  &__wrapper {
    @include container;
  }

  &__info {
    z-index: 1;
    position: relative;
    text-align: center;

    & p {
      padding-bottom: 48px;
      font-weight: 300;
      font-size: 20px;
      line-height: 26px;
      letter-spacing: 4px;
      text-transform: uppercase;

      @include fluid-type(420px, 1920px, 12px, 20px);

      color: #ffffff;

      text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);

      @media (max-width: $mobile) {
        padding-bottom: 35px;
        font-size: 12px;
        line-height: 16px;
      }
    }

    & h1 {
      padding-bottom: 27px;
      @include heroTitle;

      @media (max-width: $mobile) {
        padding-bottom: 18px;
      }
    }

    & h2 {
      padding-top: 19px;
      @include heroTitle;

      @media (max-width: $mobile) {
        padding-top: 14px;
      }

      &::before {
        position: absolute;
        content: "";
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 99px;
        height: 1px;
        background-color: $c-gold;
      }
    }
  }
}
