.page-footer {
  padding-top: rem(57px);

  padding-bottom: rem(50px);

  background-color: #edece7;

  @media (max-width: $mobile) {
    padding-top: 48px;
  }

  &__wrapper {
    @include container;
    max-width: 1920px;
    margin: 0 auto;
  }

  &__logo-and-nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 37px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.14);

    @media (max-width: 1280px) {
      flex-direction: column;
      justify-content: center;
    }
  }

  &__logo {
    width: 272px;
    padding-right: 50px;

    @media (max-width: 1280px) {
      width: 100%;
      padding-right: 0;
      text-align: center;
    }
  }

  &__nav {
    margin-left: auto;
    max-width: calc(100% - 300px);

    @media (max-width: 1280px) {
      width: 100%;
      max-width: 100%;
      margin: 0 auto;
      padding-top: 40px;

      display: flex;
      justify-content: center;
    }

    @media (max-width: 959px) {
      display: none;
    }
  }

  &__bottom {
    padding-top: 35px;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 535px) {
      flex-direction: column;
    }

    & p {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;

      color: rgba(38, 42, 47, 0.59);

      @media (max-width: 535px) {
        padding-bottom: 18px;
      }
    }

    & a {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;

      color: rgba(38, 42, 47, 0.59);
      text-decoration: none;

      @include hover;

      &:not(:last-child) {
        @media (max-width: 535px) {
          padding-bottom: 18px;
        }
      }
    }
  }
}
