.page {
  position: relative;

  min-height: 100%;

  margin: 0;
  padding: 0;
  font-family: $font;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
  color: $c-black;

  background-color: #f7f5f1;
  scroll-behavior: smooth;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
