.nav {
  &__list {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;

    .page-header & {
      @media (max-width: 1180px) {
        flex-direction: column;
        justify-content: center;
        text-align: center;
        padding-top: 142px;
        padding-bottom: 167px;
      }
    }
  }

  &__item {
    &:not(:last-child) {
      padding-right: 36px;

      .page-header & {
        @media (max-width: 1180px) {
          padding-right: 0;
          padding-bottom: 44px;
        }
      }

      @media (min-width: $tablet) {
        padding-right: 36px;
      }
    }
  }

  &__link {
    padding-bottom: 8px;
    position: relative;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;

    letter-spacing: 1px;
    text-transform: uppercase;
    color: #cccbc6;

    @media (max-width: 1180px) {
      padding-bottom: 0;
    }

    .menu-open & {
      color: #757470;
    }

    .scroll-up .page-header & {
      color: $c-dark;

      &:focus,
      &:hover {
        color: #8f6b2d;
      }
    }

    .page-footer & {
      color: #131217;

      &:focus,
      &:hover {
        color: #8f6b2d;
      }
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      right: 0;
      width: 0;
      height: 1px;
      background-color: #8f6b2d;
      transition: width 0.3s ease;
    }

    &:focus,
    &:hover {
      color: #8f6b2d;

      &::before {
        width: 100%;
        right: auto;
        left: 0;
      }
    }
  }
}
