.about {
  background-color: #8b8581;

  &__wrapper {
    padding: 0;
    max-width: 1920px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
  }

  &__pic {
    width: 50%;

    @media (max-width: $mobile) {
      width: 100%;
    }

    & img {
      width: 100%;
      height: 100%;
    }
  }

  &__info {
    width: 49%;
    // padding: 152px 143px 295px 168px;
    padding: 8rem 8rem 12rem 10rem;
    color: #ffffff;
    text-align: center;

    @media (max-width: 1680px) {
      padding: 6rem 5rem;
    }

    @media (max-width: 1440px) {
      padding: 5rem 4rem;
    }

    @media (max-width: 1280px) {
      padding: 4rem 3rem;
    }

    @media (max-width: $tablet) {
      padding: 3rem 2rem;
    }

    @media (max-width: $mobile) {
      width: 100%;
      padding: 30px 37px 70px;
    }

    & h2 {
      padding-bottom: 10px;
      margin-bottom: 53px;
      @include titleNoColor;

      @media (max-width: $mobile) {
        padding-bottom: 22px;
        margin-bottom: 26px;
      }

      &::before {
        position: absolute;
        content: "";
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 127px;
        height: 3px;
        background-color: #ccbb9e;
      }
    }

    & p {
      margin-bottom: 70px;
      font-weight: 300;
      font-size: 24px;
      line-height: 36px;

      @include fluid-type(420px, 1920px, 16px, 24px);

      @media (max-width: $mobile) {
        margin-bottom: 40px;
        font-size: 16px;
        line-height: 28px;
      }
    }

    & a {
      padding: 12px 54px;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 30px;
      color: #ffffff;
      text-decoration: none;
      text-transform: uppercase;
      border: 1px solid #ffffff;
      @include hover;

      @media (max-width: $mobile) {
        padding: 15px 54px;
      }
    }
  }
}
