.directions {
  padding-top: 62px;

  @media (max-width: $mobile) {
    padding-top: 33px;
  }

  &__wrapper {
    @include container;
    max-width: 1920px;
    margin: 0 auto;
  }

  & h2 {
    padding-bottom: 10px;
    @include titleNoColor;
    color: $c-dark;

    @media (max-width: $mobile) {
      padding-top: 6px;
    }

    &::before {
      position: absolute;
      content: "";
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 127px;
      height: 3px;
      background-color: rgba(204, 187, 158, 0.28);
    }
  }

  &__items {
    // display: grid;
    // grid-template-columns: repeat(2, 1fr);
    // gap: 28px;
    display: flex;
    flex-wrap: wrap;
    padding-top: 62px;
    padding-bottom: 62px;

    @media (max-width: $mobile) {
      // grid-template-columns: 1fr;
      flex-direction: column;
      padding-top: 52px;
      padding-bottom: 5px;
    }
  }

  &__item {
    position: relative;
    width: calc(50% - 14px);
    padding-top: 512px;
    padding-bottom: 65px;
    padding-right: 1rem;
    padding-left: 1rem;
    margin-bottom: 50px;

    

    transition: 0.3s;

    &:not(:nth-child(2n)) {
      margin-right: 28px;
    }

    @media (max-width: $tablet) {
      padding-top: 350px;
    }

    @media (max-width: $mobile) {
      width: 100%;
      padding-top: 300px;
      padding-bottom: 37px;

      &:not(:nth-child(2n)) {
        margin-right: 0;
      }
    }

    @media (max-width: $mobile-xs) {
      padding-top: 186px;
      padding-left: 48px;
      padding-right: 48px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
          0deg,
          rgba(19, 18, 23, 0.82) 10.67%,
          rgba(19, 18, 23, 0) 63.44%
        ),
        linear-gradient(0deg, rgba(19, 18, 23, 0.3), rgba(19, 18, 23, 0.3));
    }

    & h3 {
      position: relative;
      font-family: $font-title;
      font-style: normal;
      font-weight: 500;

      font-size: 24px;
      line-height: 29px;
      letter-spacing: 1.48287px;

      text-align: center;
      text-transform: uppercase;

      color: #ffffff;

      @media (min-width: $mobile) {
        padding-bottom: 16px;

        font-size: 38px;
        line-height: 46px;

        letter-spacing: 2px;

        &::before {
          position: absolute;
          content: "";
          bottom: 0;
          left: 50%;
          transform: translateX(-50%);
          width: 127px;
          height: 3px;
          background-color: rgba(204, 187, 158, 0.28);
        }
      }
    }
  }

  &__pic {
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__promo-text {
    position: relative;
    margin-bottom: 67px;
    font-family: $font-title;
    font-style: normal;
    font-weight: normal;
    font-size: 38px;
    line-height: 54px;

    letter-spacing: 9.5px;
    text-transform: uppercase;

    text-align: center;

    color: #3e3d3c;

    @include fluid-type(420px, 1920px, 24px, 38px);

    @media (max-width: $mobile) {
      padding: 0 5px;
      margin-bottom: 50px;
      font-weight: normal;
      font-size: 24px;
      line-height: 34px;

      letter-spacing: 1px;

      color: #131217;
    }

    @media (min-width: $tablet) {
      padding-left: 5rem;
      padding-right: 5rem;

      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 5rem;
        width: 230px;
        height: 3px;
        background-color: $c-gold;
      }

      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        right: 5rem;
        width: 230px;
        height: 3px;
        background-color: $c-gold;
      }
    }

    @media (min-width: 1800px) {
      padding-left: 311px;
      padding-right: 233px;

      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 230px;
        width: 230px;
        height: 3px;
        background-color: $c-gold;
      }

      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        right: 230px;
        width: 230px;
        height: 3px;
        background-color: $c-gold;
      }
    }
  }

  &__pic-bg {
    width: 100%;
    height: 100%;

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
