.form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 43px;

  @media (max-width: $mobile) {
    width: 100%;
    max-width: 329px;
    margin: 0 auto;
    padding-top: 24px;
    padding-bottom: 26px;
  }

  & label {
    width: 47%;
    margin-bottom: 38px;

    @media (max-width: $mobile) {
      width: 100%;
      margin-bottom: 19px;
    }

    &.form__check {
      width: 100%;

      @media (max-width: $mobile) {
        padding-top: 10px;
      }
    }
  }

  &__check {
    width: 100%;

    & span {
      display: inline-block;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.5px;

      color: #818181;
      text-align: left;

      @media (min-width: $mobile) {
        font-size: 12px;
        line-height: 28px;
      }
    }

    & input[type="checkbox"] {
      width: 25px;
      height: 25px;
      background-color: transparent;
      border: 1px solid #c4b192;
      border-radius: 0;
      outline: none;
    }

    & input[type="checkbox"]:not(:checked),
    & input[type="checkbox"]:checked {
      position: absolute;
      left: -9999px;
    }

    & input[type="checkbox"]:not(:checked) + span,
    & input[type="checkbox"]:checked + span {
      position: relative;
      padding-left: 40px;
      cursor: pointer;
    }

    & input[type="checkbox"]:not(:checked) + span::before,
    & input[type="checkbox"]:checked + span::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 25px;
      height: 25px;
      border: 1px solid #c4b192;
      border-radius: 0;
      box-sizing: border-box;
    }

    & input[type="checkbox"]:not(:checked) + span::after,
    & input[type="checkbox"]:checked + span::after {
      content: " ";
      position: absolute;
      top: 0;
      left: 0;

      width: 25px;
      height: 25px;
      background-image: url(../img/icons/icon-check.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 14px 10px;
      background-color: #c4b192;
      transition: all 0.2s;
    }

    & input[type="checkbox"] + span::after {
      opacity: 0;
      transform: scale(0);
    }

    & input[type="checkbox"]:checked + span::after {
      opacity: 1;
      transform: scale(1);
    }
  }

  & input {
    padding: 13px;
    padding-left: 40px;
    width: 100%;

    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 28px;

    letter-spacing: 1px;

    color: #5f5d65;

    background-color: transparent;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #4b4a50;
    outline: none;

    background-repeat: no-repeat;
    background-position: 0 50%;
    background-size: 18px 18px;

    &:focus,
    &:hover,
    &:active {
      outline: none;
    }
  }

  &__name {
    & input {
      background-image: url(../img/icons/icon-person.svg);
    }
  }

  &__phone {
    & input {
      background-image: url(../img/icons/icon-phone_.svg);
    }
  }

  & button[type="submit"] {
    @include btn-reset;

    padding: 12px 39px;
    margin: 0 auto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 30px;

    text-transform: uppercase;

    color: #4b4a50;
    border: 1px solid #ccbb9e;

    &:focus,
    &:hover,
    &:active {
      outline: none;
    }
  }
}
