.contacts {
  padding-top: 92px;

  background-color: #f0ece3;

  @media (max-width: $mobile) {
    padding-top: 50px;
    padding-bottom: 76px;
  }

  &__wrapper {
    @include container;
    max-width: 933px;
    margin: 0 auto;
    text-align: center;
  }

  & h2 {
    margin-bottom: 38px;
    padding-bottom: 16px;
    @include titleNoColor;
    @include lineBeforeButtom;

    @media (max-width: $mobile) {
      margin-bottom: 26px;
    }
  }

  & p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 28px;

    letter-spacing: 1px;

    color: #818181;
  }

  &__form {
    padding-bottom: 67px;
  }

  &__map {
    position: relative;
    min-height: 553px;

    padding-top: 134px;
    padding-bottom: 134px;
    background-image: url(../img/contacts/map-mob.jpg);
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-width: $tablet) {
      background-image: url(../img/contacts/map.jpg);
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 39px 46px;
    max-width: 291px;
    margin-left: auto;
    margin-right: 15%;
    background: #ffffff;
    box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.1);

    text-align: center;

    color: #000000;

    @media (max-width: $mobile) {
      position: absolute;
      bottom: -85px;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      max-width: 361px;
      padding: 39px 35px;
    }

    & p {
      padding-bottom: 18px;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 28px;
      text-align: center;
      letter-spacing: 1px;
      text-transform: uppercase;

      color: #000000;
    }
  }

  &__phone {
    position: relative;
    color: #918673;
    padding-bottom: 29px;
    margin-bottom: 29px;

    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;

    letter-spacing: 1px;
    text-transform: uppercase;

    color: #8f6b2d;

    &::before {
      position: absolute;
      content: "";
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 84px;
      height: 1px;
      background-color: #918673;
    }
  }
}
