.stages {
  padding-top: 87px;
  padding-bottom: 90px;
  background-color: #dddcd7;

  @media (max-width: $mobile) {
    padding-top: 48px;
    padding-bottom: 57px;
  }

  &__wrapper {
    max-width: 1195px;
    margin: 0 auto;
    @include container;
  }

  &__header {
    text-align: center;
    padding-bottom: 59px;

    @media (max-width: $mobile) {
      padding-bottom: 48px;
    }

    & p {
      padding-bottom: 10px;
      font-style: normal;
      font-weight: normal;

      font-size: 12px;
      line-height: 14px;
      letter-spacing: 0.19em;
      text-transform: uppercase;

      color: #131217;
    }

    & h2 {
      position: relative;
      padding-bottom: 15px;
      font-family: $font-title;
      font-style: normal;
      font-weight: 500;
      font-size: 38px;
      line-height: 46px;
      text-align: center;
      letter-spacing: 2px;
      text-transform: uppercase;

      color: #131217;

      @include fluid-type(420px, 1920px, 28px, 38px);

      @media (max-width: $mobile) {
        font-size: 28px;
        line-height: 34px;
      }

      &::before {
        position: absolute;
        content: "";
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 127px;
        height: 3px;
        background-color: #ccbb9e;
      }
    }
  }

  &__list {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 59px;
    padding-left: 35px;

    @media (max-width: 960px) {
      flex-direction: column;
      padding-left: 0;
      padding-bottom: 40px;
    }
  }

  &__item {
    position: relative;

    font-family: $font-title;
    font-style: italic;
    font-weight: 500;
    font-size: 21px;
    line-height: 25px;
    text-align: center;
    letter-spacing: 5px;
    text-transform: uppercase;

    color: #131217;

    @include fluid-type(420px, 1920px, 16px, 21px);

    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 19px;
    }

    &:not(:last-child) {
      position: relative;
      padding-right: 102px;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);

        width: 92px;
        height: 20px;

        background-image: url(../img/icons/icon-arrow.svg);
        background-repeat: no-repeat;
        background-position: 100% 50%;
        background-size: 150%;
      }

      @media (max-width: $tablet) {
        padding-right: 80px;

        &::before {
          width: 70px;
        }
      }

      @media (max-width: 960px) {
        padding-right: 0;
        padding-bottom: 90px;
        &::before {
          top: auto;
          bottom: 30px;
          left: 50%;

          transform: translateX(-50%) rotate(90deg);
          width: 40px;
          background-size: auto;
        }
      }

      @media (max-width: $mobile) {
        padding-right: 0;
        padding-bottom: 77px;
      }
    }
  }

  &__text {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 35px;

    @media (max-width: $mobile) {
      flex-direction: column;
      padding-left: 0;
    }

    & p {
      width: 47%;
      padding-bottom: 19px;
      font-style: normal;
      font-weight: 300;
      font-size: 18px;
      line-height: 30px;

      text-align: justify;

      color: #000000;

      @include fluid-type(420px, 1920px, 16px, 18px);

      @media (max-width: $mobile) {
        width: 100%;
        font-size: 16px;
        line-height: 28px;
      }
    }
  }
}
